
"use client";

import { useState, useEffect, useRef } from 'react';
import { Download, Upload, Settings, FileText, Languages, Loader2, Mic } from 'lucide-react';
import {
  SidebarProvider,
  Sidebar,
  SidebarInset,
  SidebarHeader,
  SidebarContent,
  SidebarTrigger,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
} from '@/components/ui/sidebar';
import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from '@/components/ui/dialog';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { ScrollArea } from '@/components/ui/scroll-area';
import { useToast } from '@/hooks/use-toast';
import { exportToSrt } from '@/lib/srt';
import type { TranscriptWord } from '@/lib/types';
import { translateText } from '@/ai/flows/translate-text-flow';
import { transcribeAudio } from '@/ai/flows/speech-to-text-flow';
import { EditorView } from './editor-view';
import { TranscriptView } from './transcript-view';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';

const blobToBase64 = (blob: Blob): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      if (typeof reader.result === 'string') {
        resolve(reader.result);
      } else {
        reject(new Error('Failed to convert blob to Base64.'));
      }
    };
    reader.onerror = reject;
    reader.readAsDataURL(blob);
  });
};


export function VoiceScribePage() {
  const [audioFile, setAudioFile] = useState<File | null>(null);
  const [audioUrl, setAudioUrl] = useState<string | null>(null);
  const [isTranscribing, setIsTranscribing] = useState(false);
  const [structuredTranscript, setStructuredTranscript] = useState<TranscriptWord[]>([]);
  const [editedTranscript, setEditedTranscript] = useState('');
  const [audioDuration, setAudioDuration] = useState(0);

  const [isTranslating, setIsTranslating] = useState(false);
  const [targetLanguage, setTargetLanguage] = useState('Khmer');
  const [translatedText, setTranslatedText] = useState('');
  const [isTranslationDialogOpen, setIsTranslationDialogOpen] = useState(false);

  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    const processAudio = async () => {
        if (!audioFile) return;
        
        const newAudioUrl = URL.createObjectURL(audioFile);
        setAudioUrl(newAudioUrl);

        const audio = new Audio(newAudioUrl);
        audio.onloadedmetadata = () => setAudioDuration(audio.duration);

        setIsTranscribing(true);
        setStructuredTranscript([]);
        setEditedTranscript('');

        try {
            const audioDataUri = await blobToBase64(audioFile);
            const result = await transcribeAudio({ audioDataUri });
            
            if (result && result.transcript) {
                setStructuredTranscript(result.transcript);
                setEditedTranscript(result.text);
            } else {
                toast({
                    title: "",
                    description: " ",
                    variant: "destructive",
                });
            }
        } catch (e: any) {
            console.error(e);
            toast({
                title: "",
                description: e.message || "",
                variant: "destructive",
            });
        } finally {
            setIsTranscribing(false);
        }
    };
    processAudio();

    return () => {
        if (audioUrl) {
            URL.revokeObjectURL(audioUrl);
        }
    }
  }, [audioFile, toast]);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setAudioFile(file);
    }
  };

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const handleExport = () => {
    if (editedTranscript && audioDuration > 0) {
      exportToSrt(editedTranscript, audioDuration);
    } else {
      toast({
        title: '',
        description: '',
        variant: 'destructive',
      });
    }
  };

  const handleTranslate = async () => {
    if (!editedTranscript) {
        toast({ title: "", description: "", variant: "destructive" });
        return;
    }
    setIsTranslating(true);
    setTranslatedText('');
    setIsTranslationDialogOpen(true);
    try {
        const result = await translateText({ text: editedTranscript, targetLanguage });
        setTranslatedText(result.translatedText);
    } catch (e) {
        toast({ title: "", description: " ", variant: "destructive" });
        setIsTranslationDialogOpen(false);
    } finally {
        setIsTranslating(false);
    }
  };

  const isReadyForContent = !isTranscribing && structuredTranscript.length > 0;

  return (
    <SidebarProvider>
      <Sidebar>
        <SidebarHeader>
            <div className="flex items-center gap-3 p-2">
                <FileText className="text-primary h-6 w-6"/>
                <h2 className="text-xl font-semibold text-sidebar-foreground">VoiceScribe</h2>
            </div>
        </SidebarHeader>
        <SidebarContent>
            <SidebarMenu>
                <SidebarMenuItem>
                    <SidebarMenuButton tooltip="" isActive>
                        <Settings />
                        <span></span>
                    </SidebarMenuButton>
                </SidebarMenuItem>
            </SidebarMenu>
        </SidebarContent>
      </Sidebar>
      <SidebarInset>
        <div className="flex flex-col h-full">
            <header className="flex flex-wrap items-center justify-between gap-4 p-4 md:p-6 border-b bg-background">
                <div className="flex items-center gap-4">
                    <div className="md:hidden">
                        <SidebarTrigger />
                    </div>
                    <div>
                        <h1 className="text-2xl font-bold tracking-tight"></h1>
                        <p className="text-muted-foreground">  </p>
                    </div>
                </div>
                <div className="flex items-center gap-2">
                     <input
                        type="file"
                        ref={fileInputRef}
                        onChange={handleFileChange}
                        className="hidden"
                        accept="audio/*"
                    />
                    <Button 
                        onClick={handleUploadClick} 
                        disabled={isTranscribing}
                    >
                        {isTranscribing ? <Loader2 className="animate-spin" /> : <Upload />}
                        <span className="ml-2 hidden sm:inline">{isTranscribing ? '...' : ''}</span>
                    </Button>
                     <Button 
                        onClick={handleExport}
                        variant="outline"
                        disabled={!isReadyForContent}
                    >
                        <Download />
                         <span className="ml-2 hidden sm:inline"></span>
                    </Button>
                    <Select value={targetLanguage} onValueChange={setTargetLanguage} disabled={!isReadyForContent}>
                        <SelectTrigger className="w-[120px]">
                            <SelectValue placeholder="" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="Khmer"></SelectItem>
                            <SelectItem value="Spanish"></SelectItem>
                            <SelectItem value="French"></SelectItem>
                            <SelectItem value="German"></SelectItem>
                            <SelectItem value="Japanese"></SelectItem>
                            <SelectItem value="Mandarin"></SelectItem>
                        </SelectContent>
                    </Select>
                    <Button 
                        onClick={handleTranslate}
                        variant="outline"
                        disabled={!isReadyForContent || isTranslating}
                    >
                        {isTranslating ? <Loader2 className="animate-spin" /> : <Languages />}
                         <span className="ml-2 hidden sm:inline"></span>
                    </Button>
                </div>
            </header>
            <main className="flex-grow p-4 md:p-6 bg-muted/20">
                <Tabs defaultValue="edit" className="flex flex-col h-full">
                    {isTranscribing ? (
                        <div className="space-y-4 flex-grow flex flex-col items-center justify-center">
                          <Loader2 className="h-12 w-12 animate-spin text-primary" />
                          <p className="text-muted-foreground mt-4"> ...</p>
                        </div>
                    ) : (
                      <>
                        {structuredTranscript.length === 0 ? (
                            <div className="flex flex-col items-center justify-center text-center flex-grow rounded-lg border-2 border-dashed border-muted-foreground/20 bg-background h-full">
                                <Mic className="w-20 h-20 text-muted-foreground/30 mb-4"/>
                                <h3 className="text-2xl font-semibold"></h3>
                                <p className="text-muted-foreground mt-2 mb-4"> </p>
                                <Button onClick={handleUploadClick} variant="outline" size="lg">
                                    <Upload className="mr-2"/>
                                    
                                </Button>
                            </div>
                        ) : (
                            <>
                            <TabsList className="grid w-full grid-cols-2 mb-4 max-w-sm mx-auto">
                                <TabsTrigger value="edit"></TabsTrigger>
                                <TabsTrigger value="sync"></TabsTrigger>
                            </TabsList>
                            <TabsContent value="edit" className="m-0 flex-grow bg-background rounded-md border shadow-sm">
                                <EditorView
                                    transcript={editedTranscript}
                                    onTranscriptChange={setEditedTranscript}
                                    disabled={!isReadyForContent}
                                />
                            </TabsContent>
                            <TabsContent value="sync" className="m-0 flex-grow bg-background rounded-md border shadow-sm">
                                <TranscriptView
                                    structuredTranscript={structuredTranscript}
                                    audioUrl={audioUrl}
                                />
                            </TabsContent>
                            </>
                        )}
                      </>
                    )}
                </Tabs>
            </main>
        </div>
      </SidebarInset>
      
      <Dialog open={isTranslationDialogOpen} onOpenChange={setIsTranslationDialogOpen}>
        <DialogContent className="sm:max-w-[625px]">
          <DialogHeader>
            <DialogTitle> {targetLanguage}</DialogTitle>
            <DialogDescription>
               
            </DialogDescription>
          </DialogHeader>
          <div className="py-4">
            {isTranslating ? (
                <div className="flex items-center justify-center h-48">
                    <Loader2 className="h-8 w-8 animate-spin text-primary" />
                </div>
            ) : (
                <ScrollArea className="h-48 w-full rounded-md border p-4">
                    {translatedText}
                </ScrollArea>
            )}
          </div>
        </DialogContent>
      </Dialog>
    </SidebarProvider>
  );
}
