function formatTime(totalSeconds: number): string {
  const hours = Math.floor(totalSeconds / 3600);
  const minutes = Math.floor((totalSeconds % 3600) / 60);
  const seconds = Math.floor(totalSeconds % 60);
  const milliseconds = Math.round((totalSeconds - Math.floor(totalSeconds)) * 1000);

  const pad = (num: number) => num.toString().padStart(2, '0');
  const padMs = (num: number) => num.toString().padStart(3, '0');

  return `${pad(hours)}:${pad(minutes)}:${pad(seconds)},${padMs(milliseconds)}`;
}

export function exportToSrt(text: string, duration: number): void {
  if (!text.trim() || duration <= 0) {
    alert("No text or audio duration to export.");
    return;
  }
  
  const lines = text.trim().split('\n').filter(line => line.trim() !== '');
  const numLines = lines.length;
  const timePerLine = duration / numLines;
  
  let srtContent = '';
  for (let i = 0; i < numLines; i++) {
    const startTime = i * timePerLine;
    const endTime = (i + 1) * timePerLine;
    
    srtContent += `${i + 1}\n`;
    srtContent += `${formatTime(startTime)} --> ${formatTime(endTime)}\n`;
    srtContent += `${lines[i]}\n\n`;
  }
  
  const blob = new Blob([srtContent], { type: 'text/plain' });
  const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = 'transcript.srt';
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
  URL.revokeObjectURL(url);
}
